const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || "http://localhost:5185"

export const apiClient = {
  async request(url, options = {}) {
    const token = typeof window !== "undefined" ? localStorage.getItem("accessToken") : null

    const headers = {
      "Content-Type": "application/json",
      ...options.headers,
    }

    if (token) {
      headers.Authorization = `Bearer ${token}`
    }

    const config = {
      ...options,
      headers,
    }

    try {
      const response = await fetch(`${API_BASE_URL}${url}`, config)

      // Một số lỗi (401, 500) có thể trả body rỗng hoặc không phải JSON
      const text = await response.text()
      let data = null
      if (text) {
        try {
          data = JSON.parse(text)
        } catch {
          // Không phải JSON, giữ nguyên data = null
        }
      }

      if (!response.ok) {
        const message =
          data?.message ||
          (response.status === 401
            ? "Bạn chưa đăng nhập hoặc token hết hạn"
            : `Có lỗi xảy ra (HTTP ${response.status})`)
        throw new Error(message)
      }

      return data
    } catch (error) {
      if (error instanceof Error) {
        throw error
      }
      throw new Error("Không thể kết nối đến server")
    }
  },

  get(url, options = {}) {
    return this.request(url, { ...options, method: "GET" })
  },

  post(url, data, options = {}) {
    return this.request(url, {
      ...options,
      method: "POST",
      body: JSON.stringify(data),
    })
  },

  put(url, data, options = {}) {
    return this.request(url, {
      ...options,
      method: "PUT",
      body: JSON.stringify(data),
    })
  },

  delete(url, options = {}) {
    return this.request(url, { ...options, method: "DELETE" })
  },
}

